import React from "react";
import ReactDOM from "react-dom";
import classnames from "classnames";
import {
    FIELD_RATING,
    FIELD_RUN_TIME,
    FIELD_FORMATS,
    FIELD_TITLE_NAME
} from "../calendar-field-types";

class ReleaseList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            showMoreRelease: false 
        };
    }

    getFieldValue = (release, fieldName) => {
        return release[fieldName] && release[fieldName].value;
    };

    renderListItem = (release, idx) => {
        const title = this.getFieldValue(release, FIELD_TITLE_NAME);
        const [ titleName, formats ] = title && title.split(/\s*--\s*/) || [];
        let titleInfo = [
            FIELD_RATING,
            FIELD_RUN_TIME
        ].map(f => this.getFieldValue(release, f));

        titleInfo.unshift(formats);
        titleInfo = titleInfo.filter(v => !!v)
                             .join(", ");
        
        return (
            <li key={idx}>
                { titleName && <span className="rel-title-name">{titleName}</span> }
                { titleInfo && <span className="rel-title-info">{titleInfo}</span> }
            </li>
        );
    };

    render = () => {
        const { 
            className, 
            head,
            data,
            maxItemsToShow
        } = this.props;
        const classes = classnames(className, "release-list");
        const releasesToShow = data.slice(0, maxItemsToShow);
        const restReleases = data.slice(maxItemsToShow, data.length); 

        return (
            <ul className={classes}>
                {releasesToShow.map(this.renderListItem)}
                {restReleases.length > 0 && (
                    <div className="more-releases">
                        {restReleases.map(this.renderListItem)}
                    </div>
                )}
            </ul>
        );
    };
}

ReleaseList.defaultProps = {
    className: "",
    maxItemsToShow: 5,
    data: []
};

export default ReleaseList;
