import React from "react";
import ReactDOM from "react-dom";
import TableHead from "./table/TableHead";
import TableBody from "./table/TableBody";
import TableRow from "./table/TableRow";
import TableCell from "./table/TableCell";
import classnames from "classnames";

class CalendarGrid extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            focusCell: {}
        };
    }

    handleMouseOverCell = (cellPos, data, event) => {
        if (this.props.highlight) {
            this.setState({ focusCell: cellPos });
        }
    };

    render = () => {
        const { focusCell } = this.state;
        const {
            data : [headers, ...rows],
            highlight,
            className
        } = this.props;

        const classes = classnames(
            className,
            "calendar-grid"
        );

        return ( 
            <div className={classes}>
                <div className="cg-view-container">
                    { headers && 
                    <table className="cg-table">
                        <TableHead>
                            <TableRow>
                                {headers.map((header, idx) => (
                                    <TableCell head key={idx} className={header.className}>
                                        {this.props.headerRender(header)}
                                    </TableCell>
                                ))}
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {rows.map((row, rowIdx) => (
                                    <TableRow key={rowIdx}>
                                        {row.map((data, colIdx) => (
                                            <TableCell
                                                className={highlight && (focusCell.row === rowIdx || focusCell.col === colIdx) ? "hl-cell" : ""}
                                                key={colIdx}
                                                onMouseEnter={highlight ? this.handleMouseOverCell.bind(this, { row: rowIdx, col: colIdx }, data) : null}
                                            >
                                                {this.props.cellRender(data)} 
                                            </TableCell>
                                        ))}
                                    </TableRow>
                                )
                            )}
                        </TableBody>
                    </table>
                    }
                </div>
            </div>
        );
    };
}

CalendarGrid.defaultProps = {
    transpose: false,
    cellRender: (data) => { return data && data.value; },
    highlight: true,
    onCellHover: null,
    data: []
};

export default CalendarGrid;
