import React from "react";
import ReactDOM from "react-dom";
import CalendarGrid from "./components/CalendarGrid";
import ReleaseList from "./components/ReleaseList";
import data from "./data/calendar-grid-data";
import zipObject from "lodash/zipObject";
import moment from "moment";
import "../css/calendar-grid.scss";
import {
    FIELD_DISTRIBUTOR_LOGO,
    FIELD_DISTRIBUTOR_NAME,
    FIELD_DISTRIBUTOR_LONG_NAME,
    FIELD_RELEASE_DATE
} from "./calendar-field-types";

const RAW_DATE_FORMAT = "YYYYMMDD hh:mm:ss";
const DATE_FORMAT = "MMM DD";

const headerRender = (config, data) => {
    let content;

    if (data.name === "dist_logo") {
        content = (
            <img src={data.value} alt={data.alt} /> 
        );
    } else {
        content = <span className="cell-title">{data.value}</span>;
    }

    return content;
};

const cellRender = (config, data) => {
    let content;
    if (data.name === "release_date") {
        const date = moment(data.value, RAW_DATE_FORMAT);
        if (date.isValid()) {
            content = (
                <React.Fragment>
                <span className="cell-title">{date.format(DATE_FORMAT)}</span><br/>
                <span className="cell-subtitle">{date.year()}</span><br/>
                </React.Fragment>
            );
        } else {
            content = <span className="cell-title">{data.value}</span>;
        }
    } else if (data.name === "dist_name") {
        content = <span className="cell-title">{data.value}</span>;
    } else {
        content = <ReleaseList data={data.value || []} />;
    }
        
    return content;
};

const prepareData = (queryResults, config) => {
    const {
        dimensions
    } = config.query_fields;
    const dimensionByName = dimensions.reduce((hash, dimension) => {
        return { 
            ...hash, 
            [ dimension.name ]: {
                ...dimension,
                short_name: dimension.name.split(".").pop()
            }
        };
    }, {});

    const hasLogo = dimensionByName[FIELD_DISTRIBUTOR_LOGO];
    const canRender =  dimensionByName[FIELD_RELEASE_DATE]
                    && (dimensionByName[FIELD_DISTRIBUTOR_NAME] || dimensionByName[FIELD_DISTRIBUTOR_LONG_NAME])
                    && queryResults.length > 0;
 
    if (canRender) {
        const distByName = {};

        //organize data by release_date and distributor_name  
        const dataByRelDateDist = queryResults.reduce((hash, row) => {
            const relDate = row[FIELD_RELEASE_DATE].value;
            const distName = row[FIELD_DISTRIBUTOR_LONG_NAME] && row[FIELD_DISTRIBUTOR_LONG_NAME].value
                          || row[FIELD_DISTRIBUTOR_NAME].value;
            const distLogo = hasLogo && row[FIELD_DISTRIBUTOR_LOGO].value;
            hash[relDate] = hash[relDate] || {};

            if (distName) {
                distByName[distName] = {
                    name: "dist_name",
                    value: distName,
                    logo: distLogo
                };

                hash[relDate][distName] = hash[relDate][distName] || [];
                hash[relDate][distName].push(row);
            }

            return hash;
        }, {});

        const distributors = Object.values(distByName).sort((a, b) => {
            if (a.value < b.value) return -1;
            if (a.value > b.value) return 1;
            return 0;
        });
        const distLogoColumns = hasLogo && [{ name: "blank", value: "", className: "dist-logo" }].concat(
            distributors.map(dist => {
                return {
                    name: "dist_logo",
                    value: dist.logo,
                    alt: dist.value,
                    className: "dist-logo"
                };
            })
        );
        const columns = [{ name: "release_date", value: "Week" }].concat(distributors);
        const releaseDates = Object.keys(dataByRelDateDist).sort();
        const rows = releaseDates.map(relDate => {
            return columns.map(col => {
                const isRelDate = col.name === "release_date";
                return {
                    name: isRelDate ? col.name : "releases",
                    value: isRelDate ? relDate : dataByRelDateDist[relDate][col.value]
                };
            });
        });

        return distLogoColumns 
            ? [ distLogoColumns, columns ].concat(rows)
            : [ columns ].concat(rows);
    }

    return [];
};

looker.plugins.visualizations.add({
    options: {
        maxTitleToShow: {
            type: "number",
            label: "Max # of Titles to Show"
        },
        highlight: {
            type: "boolean",
            label: "Highlight",
            default: false
        },
        wrapTableContent: {
            type: "boolean",
            label: "Wrap Table Content",
            default: false
        }
    },
    create: function(element, config){
        console.log("CONFIG", config);
    },
    updateAsync: function(data, element, config, queryResponse, details, doneRendering){
        const preparedData = prepareData(data, config);
        ReactDOM.render(
            <CalendarGrid 
                className={config.wrapTableContent ? "wrap-table-content" : ""}
                data={preparedData} 
                highlight={config.highlight} 
                cellRender={cellRender.bind(undefined, config)}
                headerRender={headerRender.bind(undefined, config)}
            />, 
            element
        );
        console.log("UPDATE ASYNC IS CALLED", data,element,config, queryResponse, details, doneRendering);
        doneRendering();
    }
});
